@import "variables";

.pokecard-full { //is already set to flex
    position: fixed;
    display: none;
    flex-direction: row;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 92.5%;
    height: 90%;
    border-radius: 8px;
    font-family: $font-primary;

    @include respond(laptop-1) {
        width: 97.5%;
    }
    @include respond(tablet-1) {
        height: 97.5%;
        flex-wrap: wrap;
    }

    z-index: 500;

    &--exit { //don't re-arrange for MQ
        position: absolute;
        margin-right: 2.25rem;
        margin-top: -0.5rem;
        right: 0;
        top: 0;
        font-size: 5rem;

        @include respond(tablet-1) {
            z-index: 1000;
            margin-right: 1rem;
            margin-top: -1rem;
        }

        cursor: pointer;
    }

    &__general-info {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        width: 25%;
        @include respond(laptop-1) { width: 22.5% };
        @include respond(laptop-2) { width: 25% };
        height: 80%;
        font-size: 4rem;
        margin-left: 1rem;

        @include respond(tablet-1) {
            width: 40%;
            height: 60%;
            margin-left: 0;
            padding-left: 1rem;
        }
        @include respond(phone-1) {
            height: 55%;
        }

        &__type-container {
            display: flex;
            flex-direction: column;

            span {
                margin-right: 1rem;
            }

            div {
                display: flex;
                flex-direction: row;
            }

            &--type {
                display: inline-block;
                margin-top: 0.65rem;
                padding: 0.5rem;
                padding-left: 1rem;
                padding-right: 1rem;
                border-radius: 20px;
                text-align: center;
                font-size: 2.5rem;
                text-transform: capitalize;

                &:not(:last-child) {
                    margin-right: 1rem;
                }
            }
        }

        &__ability-container {
            display: flex;
            flex-direction: column;

            span {
                margin-right: 1rem;
            }

            .ability-flex-container {
                display: flex;
                flex-direction: column;
                width: 75%;
            }

            &--ability {
                display: inline-block;
                margin-top: 0.65rem;
                padding: 0.5rem;
                padding-left: 1rem;
                padding-right: 1rem;
                border-radius: 20px;
                text-align: center;
                font-size: 2.5rem;
                text-transform: capitalize;
            }
        }
    }

    &__visual {
        display: flex;
        flex-direction: column;
        width: 45%;
        height: 100%;

        @include respond(tablet-1) {
            width: 60%;
            height: 60%;
        }
        @include respond(phone-1) {
            height: 55%;
        }

        &--name {
            height: 10%;
            margin: 0 auto;
            text-align: center;
            text-transform: capitalize;
            font-size: 5rem;
            letter-spacing: 1.5px;
            @include respond(tablet-1) {
                height: 5%;
                order: 1;
                letter-spacing: 0.2rem;
                line-height: 0.9;
                transform: translateY(-1.5rem);
            }
        }

        &--name-jp {
            position: relative;
            left: 50%;
            top: 40%;
            transform: translate(-50%, -50%);
            text-align: center;
            font-family: $font-jp;
            font-weight: 800;
            font-size: 6rem;
            opacity: 0.3;
            text-shadow: 2px 2px 2px rgba(black, 0.5);
        }

        &--sprite {
            height: 90%;
            width: 100%;

            @include respond(tablet-1) {
                height: 87.5%;
                order: 0;
            }

            animation: spriteJump 750ms;
        }
    }
}

@keyframes spriteJump {
    0% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-4rem);
    }
    55% {
        transform: translateY(0);
    }
    70% {
        transform: translateY(-3.5rem);
    }
    80% {
        transform: translateY(0);
    }
}